.pageName {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    display: flex;
    align-items: center;
    /*border: 1px solid black;*/
    padding: 1rem 0;
    color: var(--secondColor);
}

.profileInform {
    display: flex;
    align-items: center;
    flex-direction: row;
    border-radius: var(--borderRadiusCustom);
    height: 220px;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../../../Assets/walp.jpg");
    background-size: cover;
    padding: 1rem;
}

.profileInform:after {
    background: rgba(0, 0, 0, 1.2);
}

.profileInfoLeft {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 50%;
}

.profileInfoRight {
    width: 50%;
}

.userLogo {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 200px;
    width: 200px;
    padding: 10px 30px 10px 10px;

    transition: ease-in-out 0.2s;
}

.userLogo:hover {
    transform: scale(1.05);
}

.avatarUser {
    width: 100%;
    height: auto;
    border-radius: 15px;
    /*border: 5px solid white;*/
}

.profileInfoTextBox {
    padding: 10px 0 10px 0;
}

.userName {
    color: white;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 38px;
    margin: 0;
}

.userAboutMe {

    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;
}

.editStatusInput {
    font-size: 30px;
    padding: 10px;
    height: 40px;
    width: 400px;
}

.userLocation {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 23px;
}

.lookingForAJob {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
}

.userWebsite {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
}

.userWebsite a {
    text-decoration: none;
    color: white;
    transition: 0.2s;
}

.userWebsite a:hover {
    color: var(--generalColor);
}

.userSlogan {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-style: normal;
    font-size: 30px;
    font-weight: 700;
}

.editStatusInput {
    font-size: 30px;
    font-weight: 700;
    padding: 10px;
    height: 40px;
    width: 90%;
    border-radius: var(--borderRadiusCustom);
    border: 2px solid var(--generalColor);
    color: var(--generalColor);
}